header{
    display: grid;
    grid-template-columns: 2fr 1fr;
    background: linear-gradient(90deg, rgba(131,58,180,0.40689779329700626) 0%, rgba(253,29,245,0.3508753843334209) 50%, rgba(69,187,252,0.5749650201877626) 100%);
    padding: 5px;
    padding-left: 50px;
    padding-right: 50px;
}

nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    
}
nav img{
    width: 80px;
}

.logo{
    color: white;
}

.nav_item{
    background-color: rgba(0, 0, 0, 0);
    font-size: 30px;
    border: none;
    cursor: pointer;
    text-decoration: none;
}

.nav_item:hover{
    transform: scale(1.1);
}

.cart_btn{
    display: grid;
    grid-template-columns: 2fr 0.5fr;
}

.count_cart_items{
    font-size: 26px;
    font-weight: bold;
    background: rgba(255, 255, 255, 0);
    padding-left: 5px;
    color: #f8b3fc;
    margin-top: 40px;
}