footer{
    display: grid;
    grid-template-columns: 1fr 2fr;
    background: linear-gradient(90deg, rgba(69,187,252,1) 0%, rgba(253,29,248,0.2164216028208158) 50%, rgba(131,58,180,0.49653364763874297) 100%);
    justify-items: center;
    align-items: center;
}

footer p{
    color: white;
}

footer .logo{
    font-size: 45px;
    font-weight: bold;
    color: white;
}