.product_item{
    display: grid;
    border-radius: 35px;
    padding-left: 10px;
    padding-bottom: 5px;
    margin: 10px;
    transition: box-shadow 0.4s ease-out;
}

.product_item:hover{
    box-shadow: 0px 0px 20px 0px #f68dcc8a;
}

.product_item p{
    margin-bottom: 5px;
    margin-top: 5px;
    padding-left: 20px;
}

.product_img{
    justify-self: center;
    width: 300px;
}

.product_price{
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    color:  rgba(69, 142, 252, 0.647) ;
    padding-left: 20px;
}

.product_price span{
    font-weight: bold;
    font-size: 25px;
}
.product_price button{
    border: none;
    background-color: #ffffff00 !important;
}
.plus_btn{
    justify-self: center;
    cursor: pointer;
}
.plus_btn :hover{
transform: scale(1.1);
}
.plus_btn img{
    width: 40px;
}

.check_btn{
    justify-self: center;
    border-radius: 10px;
    cursor: pointer;
}
.check_btn img{
    width: 40px;
}

.favorite_btn{
    font-size: 14px;
    text-align: start;
    cursor: pointer;
}
.favorite_btn img{
    width: 35px;
}

.favorite_btn:hover{
    transform: scale(1.1);
}

.favorite_btn_added{
    border-radius: 10px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
}
.favorite_btn_added img{
    width: 40px;
}
.favorite_btn_added:hover{
    transform: scale(1.1);
}