.close_btn{
    width: fit-content;
    justify-self: end;
    align-self: center;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
    border: none;
}
.close_btn img{
    width: 30px;
}

.cart_list{
    overflow: auto;
}

.cart_item{
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    border: 1px solid #9D9D9D;
    border-radius: 15px;
    padding: 15px;
    margin-bottom: 30px;
}

.cart_img{
    width: 70px;
}