.banner_section{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    margin: 30px;
    height: 700px;
}

.banner{
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    justify-items: end;
    align-items: center;
    background-image: url("/public/img/banner.jpg");
    background-size:cover;
    background-position: center;
    padding-top: 40px;
    padding-bottom: 40px;
    
}

.text_banner{
    color: black;
    font-size: 40px;
    font-weight: bold;
    align-self: end;
}


.banner_btn:active{
    background-color: white;
    color: #001AFF;
}